* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: url("./img/suhyeon-choi-HCDugQDdtfc-unsplash.jpg");
  background-size: cover;
  width: 100vw;
  height: 100vh;
  background-clip: border-box;
  color: white;
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
}
header {
  font-size: 2rem;
}

header,
form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: rgba(255, 255, 255, 0.294);
  cursor: pointer;
  outline: none;
  color: #fff;
}
form button {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.294);
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover {
  
  color: rgba(0, 0, 0, 0.876);
}
.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-list {
  min-width: 30%;
  list-style: none;
}

.todo {
  margin: 0.5rem;
  background: rgba(255, 255, 255, 0.37);
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
  margin-bottom: 10px;
}
.filter-todo {
  padding: 1rem;
}
.todo li {
  flex: 1;
}

.trash-btn,
.complete-btn {
  background: #000000a2;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}
.trash-btn:hover,
.complete-btn:hover{
  color: black;
}
.complete-btn {
  outline: none;
  background: rgba(0, 0, 0, 0.274);
}
.todo-item {
  padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/* Custom Select */
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
}
select {
  color: #000000;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  background: #00000096;
  cursor: pointer;
  pointer-events: none;
}
.select:hover::after {
 
}
.completed{
  color: rgb(0, 0, 0);
}